import React from "react"
import Img from "gatsby-image"
import TileBoard from "../components/tileboard"
import Tile from "../components/tile"
import presets from "../utils/presets"

const IndexPage = ({data}) =>(
  <TileBoard>
    <Tile
      imageLink={data.sunrise.childImageSharp.sizes}>
      Dawn of a new era in Veterinary Health...
      Providing better solutions for
      tomorrow's health care needs...
    </Tile>
    <Tile
      imageLink={data.cattle.childImageSharp.sizes}
    imageOnRight="true">
      Venovel healthcare was concieved as a company that provides leading-edge
      healthcare needs to eliminate the sufferings of animals at an affordable cost.
      It is founded by alumni of world's leading pharmaceutical firms
      having more than 25 years of experience working in animal healthcare.
    </Tile>
    <Tile
      imageLink={data.products.childImageSharp.sizes}
      Link="catalog"
    >
      Venovel caters a wide variety of products with our primary focus in nutritional segment.
      Proper nutrition is very vital to avoid diseases and increase productivity of cattle.
    </Tile>
    <Tile
      imageLink={data.contactUs.childImageSharp.sizes}
      imageOnRight="true"
      Link="contact"
    >
      Get in touch with us to discuss our products
    </Tile>
  </TileBoard>
)

export default IndexPage

export const IndexPageQuery = graphql`
  query IndexPageQuery {
    sunrise : file(relativePath: { eq: "assets/home_sunrise.jpg" }) {
      childImageSharp {
        sizes(maxWidth: 960) {
          ...GatsbyImageSharpSizes_withWebp
        }
      }
    }
    cattle: file(relativePath: { eq: "assets/home_cattle.jpg" }) {
      childImageSharp {
        sizes(maxWidth: 960) {
          ...GatsbyImageSharpSizes_withWebp
        }
      }
    }
    contactUs: file(relativePath: { eq: "assets/home_contact_us.jpg" }) {
      childImageSharp {
        sizes(maxWidth: 960) {
          ...GatsbyImageSharpSizes_withWebp
        }
      }
    }
    products: file(relativePath: { eq: "assets/home_products.jpg" }) {
      childImageSharp {
        sizes(maxWidth: 960) {
          ...GatsbyImageSharpSizes_withWebp
        }
      }
    }
  }
`



