import React from "react"
import Img from "gatsby-image"
import presets from "../utils/presets"
import Link from "gatsby-link"

const Tile = (props) => (
  <div css={{
    padding: `1rem`,
    borderBottom: `0.05rem`,
    borderBottomColor: presets.borderColor,
    borderBottomStyle: `solid`,
    justifyContent: `space-between`,
    "&:last-child":{
      borderBottomStyle: `none`
    }
  }}>
    <Link
      to={props.Link||"#"}
      css={{
        color: presets.brandColor,
        textDecoration: `none`,
        [presets.Tablet]: {
          display: `flex`,
        }
      }}
    >
      <div css={{
        width: `100%`,
        [presets.Tablet]: {
          width: `60%`,
          order: `${props.imageOnRight&&props.imageOnRight=="true"&&'99'}`,
          marginLeft: `${props.imageOnRight&&props.imageOnRight=="true"&&'1rem'}`,
          marginRight: `${(props.imageOnRight||'1rem')&&(props.imageOnRight!="true"&&'1rem')}`,
        },
      }}>
        <Img
          sizes={props.imageLink}/>
      </div>
      <div css={{
        [presets.Tablet]: {
          width: `40%`,
        }
      }}>
        {props.children}
      </div>
    </Link>
  </div>
)

export default Tile;



