import React from "react"
import presets from "../utils/presets"

const TileBoard = ({children}) => (
  <div css={{
    borderRadius: `1rem`,
    boxShadow: `0rem 0rem 0.1rem ${presets.borderColor}`,
    borderColor: presets.borderColor,
    backgroundColor: presets.tileBackgroundColor,
  }}>
    {children}
  </div>
)

export default TileBoard;



